import React, { useState } from 'react';
import Modal from '../../Modal/Modal';
import vkLogo from '../../../static/svg/vk.svg';
import instLogo from '../../../static/svg/instagram.svg';
import fbLogo from '../../../static/svg/alpha-f-box.svg';
import lkdnLogo from '../../../static/svg/linkedin.svg';
import gitLogo from '../../../static/svg/github.svg';
import backgroungImg from '../../../static/svg/coding.svg';

function About() {
  const [isModalOpened, setModalOpened] = useState(false);

  return (
    <div className="section s-about l-outer-width l-inner-padding"  id="about">
      <div className="s-about__content">
        <div className="s-about__left">
          <h1>
            <span>I am Denis Korneev</span>
          </h1>
          <div className="s-about__description">
            <p>I am a Web Developer located in Sochi, Russia. I currently work as a part time Remote Junior Web Developer. I am looking to take on more work and to increase my skills as a Web Developer.</p>
          </div>
          <div className="s-about__info">
            <p>
              <span>Phone</span>
              <span>+7-927-226-10-46</span>
            </p>
            <p>
              <span>Email</span>
              <span>krnvdns@outlook.com</span>
            </p>
            <p>
              <span>Adress</span>
              <span>Russia, Sochi, Gospital'naya 28, 3, 354008</span>
            </p>
            <p>
              <span>Socials</span>
              <span className="s-about__socials">
                <a href="https://vk.com/odeszaxo" target="_blank" rel="noreferrer" className="s-about__social"><img src={vkLogo} alt="#"/></a>
                <a href="https://www.instagram.com/xfat.cock.420x/" target="_blank" rel="noreferrer" className="s-about__social"><img src={instLogo} alt="#"/></a>
                <a href="https://www.facebook.com/profile.php?id=100001407669393" target="_blank" rel="noreferrer" className="s-about__social"><img src={fbLogo} alt="#"/></a>
                <a href="https://www.linkedin.com/in/%D0%B4%D0%B5%D0%BD%D0%B8%D1%81-%D0%BA%D0%BE%D1%80%D0%BD%D0%B5%D0%B5%D0%B2-432a15175/" target="_blank" rel="noreferrer" className="s-about__social"><img src={lkdnLogo} alt="#"/></a>
                <a href="https://github.com/odeszaxoxo" target="_blank" rel="noreferrer" className="s-about__social"><img src={gitLogo} alt="#"/></a>
              </span>
            </p>
            <div className="s-about__buttons">
              <button className="s-about__toggle button" onClick={() => setModalOpened(true)} href="#">Contact Me</button>
              <Modal show={isModalOpened} closeModal={() => setModalOpened(false)}></Modal>
            </div>
          </div>
        </div>
        <div className="s-about__right">
          <img className="s-about__image" alt="#" src={backgroungImg}/>
        </div>
      </div>
    </div>
  );
}

export default About;
